:root {
  --darkAccent:#316692;
  --lightAccent: #858585;
  --backgroundBase: black;
  --default-border: 1px solid darken(var(--backgroundBase, 35%));
  --shadow: 1px 1px 10px 2px darken(var(--backgroundBase, 35%));
}


body {
  background: var(--backgroundBase);
    margin: 10px 0;
}
 
#preview,
.title {
  display: inline-block;
  border: 1px solid white;
  color: white;
  background-color: var(--backgroundBase)   ;
  
}
#preview code {
  display: inline-block;
  border: 1px solid white;
  color: black;
  background-color: black   ;
}

.colorScheme {
  background-color: var(--backgroundBase);
  border: var(--default-border);
  box-shadow: var(--shadow);
  border-top: none;
}

.toolbar {
  position: relative;
  background-color:var(--darkAccent); 
  padding: 4px 4px 3px 3px;
  font-weight: bold;
  font-size: 15px;
  text-align: center;
  
}
.toolbar i {
  width: 25px;
  color: black;
  margin-left: 5px;
}
.toolbar .fa-arrows-alt,
.toolbar .fa-compress {
  position: absolute;
  right: -5px;
}


.fa-retweet:hover,
.fa-compress:hover,
.fa-arrows-alt:hover {
    color:var(--lightAccent);
    cursor: pointer;
  }


.fa-free-code-camp {
  margin-right: 3px;
}

.editorWrap {
  width: 600px;
  margin: 18px auto;
}
  .editorWrap .toolbar {
    width: 99%;
  }
   

  .editorWrap textarea {
    width: 99%;
    min-height: 200px;
    margin-bottom: -5px;
    resize: vertical;
    outline: none;
    padding-left: 5px;
    padding-top: 5px;
    font-size: 12px;
  }


.converter {
  width: 100px;
  text-align: center;
  font-size: 35px;
  margin: auto;
}

.previewWrap {
  width: 800px;
  margin: 20px auto;
  min-height: 200px;
  overflow-wrap: break-word;
  padding-right: 20px;
}
  .previewWrap .toolbar {
    left: -1px;
    width: 100%;
    padding-right: 17px;
  }
  .previewWrap #preview {
    margin-left: 5px;
    margin-top: -10px;
    width: 100%;
  }


@media screen and (max-width: 850px) {
  .previewWrap {
    width: 630px;
  }
  .editorWrap {
    width: 550px;
  }
}

.maximized {
  width: 96%;
  min-height: 100vh;
  margin: auto;
}
  .maximized textarea {
    min-height: 95vh;
    resize: none;
  }


.hide {
  display: none;
}

@media screen and (max-width: 650px) {
  body {
    margin: 5px 0;
  }
  .editorWrap {
    width: 80vw;
    margin: 0 auto;
  }
  .maximized {
    width: 95%;
    margin: auto;
  }
  .previewWrap {
    width: 95vw;
  }
  .previewWrap #preview {
      width: 100%;
  }
      .previewWrap #preview  img {
        height: 200px;
      }
  
}

/* MARKDOWN STYLES */
  #preview blockquote {
    border-left: 3px solid #224b4b;
    color: #224b4b;
    padding-left: 5px;
    margin-left: 25px;
  }

  #preview code {
    background: white;
    padding: 1px 4px 2px 4px;
    font-size: 12px;
    font-weight: bold;
    width: 95%;
    height: auto;
    overflow-x: scroll;
  }

  #preview img {
      width: 80%;
      
    }
      

  #preview pre {
    background: white;
    padding: 5px 0 5px 5px;
  }

  #preview  h1 {
    border-bottom: 2px solid var(--darkAccent);;
    text-align: center;
  }

  #preview h2 {
    border-bottom: 1px solid var(--darkAccent);;
    text-align: center;
  }

  #preview table {
    border-collapse: collapse;
    text-align: center;
  }

  #preview td,
  #preview th {
    border: 2px solid var(--darkAccent);;
    padding-left: 5px;
    padding-right: 5px;
    text-align: center;
  }

 